<template>
  <div class="back">
    <v-app-bar app elevation="2" color="#333744" dark>
      <v-app-bar-nav-icon @click="goBack()">
        <v-icon v-html="'mdi-arrow-left'"></v-icon>
      </v-app-bar-nav-icon>
      <v-toolbar-title>
        <span class="pr-8">编辑企业需求</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mb-12 mt-6" max-width="1000" min-height="1100">
          <v-form class="pl-8" :model="form">
            <div class="pt-8">
              <v-icon>mdi-tag</v-icon><span class="ml-3">项目名称</span>
            </div>
            <v-col cols="12" sm="6">
              <v-text-field
                required
                placeholder="请输入项目名称，1-20个字以内"
                :rules="[v => !!v || '项目名称为必填项']"
                v-model="form.title"
              ></v-text-field>
            </v-col>
            <div class="mt-5">
              <v-icon>mdi-clipboard-text</v-icon
              ><span class="ml-3">企业需求介绍</span>
            </div>
            <v-col cols="12" sm="8">
              <v-textarea
                filled
                v-model="form.intro"
                placeholder="请输入项目需求简介，200字以内"
                :rules="[v => !!v || '项目需求简介为必填项']"
              ></v-textarea>
            </v-col>
            <div class="mt-5">
              <v-icon>mdi-cash-usd</v-icon><span class="ml-3">资金预算</span>
            </div>
            <v-col cols="12" sm="6">
              <v-text-field
                required
                placeholder="请输入您的资金预算（元）"
                v-model="form.budget"
                :rules="[v => !!v || '资金预算为必填项']"
              ></v-text-field>
            </v-col>
            <div class="mt-5">
              <v-icon>mdi-phone</v-icon><span class="ml-3">联系方式</span>
            </div>
            <v-col cols="12" sm="6">
              <v-text-field
                required
                filled
                label="联系人姓名"
                placeholder="请输入联系人姓名"
                v-model="form.contacts"
                :rules="[v => !!v || '联系人姓名为必填项']"
              ></v-text-field>
              <v-text-field
                required
                filled
                label="联系人电话"
                placeholder="请输入联系人电话"
                v-model="form.tel"
                :rules="[v => !!v || '联系人电话为必填项']"
              ></v-text-field>
              <v-text-field
                required
                filled
                label="联系邮箱"
                placeholder="请输入联系邮箱"
                v-model="form.mail"
                :rules="[v => !!v || '联系人邮箱为必填项']"
              ></v-text-field>
            </v-col>
            <div class="mt-5">
              <v-icon>mdi-file-document-edit</v-icon
              ><span class="ml-3">需求文档</span>
            </div>
            <v-file-input
              v-model="file"
              accept="application/pdf, application/vnd.openxmlformats-officedocument.wordprocessingml.document, application/msword,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
              placeholder="只能上传pdf/docx/excel文件"
              label="需求文档上传"
              prepend-icon="mdi-paperclip"
            >
            </v-file-input>
            <v-row class="mt-8 mb-12 ml-2">
              <div class="mr-12 ml-3">
                <v-btn
                  width="120px"
                  depressed
                  large
                  color="info"
                  :loading="loading"
                  :disabled="loading"
                  @click="saveComChanges()"
                  >提交修改</v-btn
                >
              </div>
              <div class="mr-12">
                <v-btn
                  width="120px"
                  class="white--text"
                  depressed
                  large
                  color="#AAAAAA"
                  @click="cancel()"
                  >取消</v-btn
                >
              </div>
            </v-row>
          </v-form>
        </v-card>
      </v-container>
    </v-content>
    <app-footer></app-footer>
  </div>
</template>

<script>
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'CompanyEdit',
  props: ['id'],
  data() {
    return {
      file: null,
      form: {
        id: this.id,
        title: '',
        intro: '',
        budget: '',
        contacts: '',
        mail: '',
        tel: '',
        fileurl: ''
      },
      loading: false
    }
  },
  watch: {
    id: function() {
      this.getNeed()
    }
  },
  components: {
    AppFooter
  },
  methods: {
    clearInput() {
      this.form = {}
    },
    goBack() {
      this.$router.push('/company/history')
    },
    async saveComChanges() {
      let formData = new FormData()
      formData.append('File', this.file)
      let resFile = await this.$http.post('/api/business/upload', formData, {
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
      })
      if (resFile.data.code === 200) {
        this.form.fileurl = resFile.data.data
      }
      let res = await this.$http.post('/api/business/updateEnter', this.form, {
        headers: { 'Content-Type': 'application/json' }
      })
      if (res.data.code === 200) {
        this.$message.success('提交需求修改成功！')
        this.clearInput()
        this.$router.push('/company/history')
      } else {
        this.$message.error('企业需求修改失败！')
      }
    },
    async getNeed() {
      let res = await this.$http.get('/api/business/readMyEntry?id=' + this.id)
      if (res.data.code != 200) {
        this.$message.warning('抱歉，您无法编辑该企业需求！')
        return false
      }
      this.form = res.data.data.enterpriseneeds
    },
    cancel() {
      this.clearInput()
      this.$router.push('/company/history')
    }
  },
  mounted() {
    this.getNeed()
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
